.suggestions {
  text-align: left;
  z-index: 1;
  background-color: #fff;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
  list-style: none;
  position: absolute;
  top: 100%;
  left: 0;
}

.suggestions > li > a {
  clear: both;
  cursor: default;
  white-space: nowrap;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #000000bf;
  border: 0;
  padding: 5px 10px;
  font-weight: normal;
  text-decoration: none;
  display: block;
  overflow: hidden;
}

.suggestions > li.active > a, .suggestions > li > a:hover {
  color: #fff;
  cursor: pointer;
  background-color: #0d8dff;
  text-decoration: none;
}
